.Login {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 50vw 1fr;
  grid-template-areas: "... form ...";
}

.LoginForm {
  grid-area: form;
  text-align: center;
}

.LoginInput {
  text-align: center;
  margin-bottom: 1rem;
}



